<script setup lang="ts">
import type { IPlayerState } from "@pixellot/vue-player/src/util/state";
import type {
  IAnalyticsOptions,
  VideoJsPlayer as IPixellotPlayer,
  ITag as IPixellotTag,
  ITagGroup as IPixellotTagGroup,
} from "@pixellot/web-sdk";
import { Player } from "@pixellot/vue-player";
import { vOnClickOutside } from "@vueuse/components";
import { useStorage } from "@vueuse/core";
import useEventCommerce from "~/composables/events/use-event-commerce";
import { ERROR_TYPE, SEGMENT_TRACK_EVENTS } from "~/constants";
import useEventStore from "~/stores/event";
import useUserStore from "~/stores/user";
import getFloatingPosition from "~/utils/get-floating-position";

export interface IPlayerData {
  video: HTMLVideoElement;
  player: IPixellotPlayer;
  state: IPlayerState | null;
}

export interface PlayerProps {
  event: IModelEvent;
  tags?: IModelTag[];
  adsConfig?: {
    enableManifestMonitoring?: boolean;
    enableTimelineAdvancement?: boolean;
    manifestAdSource?: string;
  };
}

interface HTMLVideoElementWithAirPlay extends HTMLVideoElement {
  webkitShowPlaybackTargetPicker: () => void;
  webkitCurrentPlaybackTargetIsWireless: boolean;
}

interface IPixellotPlayerExtended extends IPixellotPlayer {
  chromecastSessionManager?: {
    getCastContext: () => {
      endCurrentSession: (value: boolean) => void;
    } | null;
  };
}

const emit = defineEmits<{
  (event: "mounted", data: IPlayerData): void;
  (event: "unmounted"): void;
  (event: "tagClick", data: { event: PointerEvent | MouseEvent; data: IPixellotTag | IPixellotTagGroup }): void;
  (event: "deleteTag", tag: IModelTag): void;
  (event: "editTag", tag: IModelTag): void;
}>();

const segment = useSegment();
const userStore = useUserStore();
const eventStore = useEventStore();
const props = defineProps<PlayerProps>();
const runtimeConfig = useRuntimeConfig().public;
const defaultVolume = useStorage("cache-pixellot-video-volume", 1);
const isPanoControlsVisible = useStorage("enable-pixellot-controls-tool-visible", true);
const { userType } = useEventCommerce(props.event.commerceCoverage);

const errorMessage = computed(() => eventStore.playerError);
const playerRef = ref<InstanceType<typeof Player> | null>(null);
const selectedTagData = ref<IModelTag[] | null>(null);
const selectedTagSelector = ref<string | null>(null);

const enableTaggingMode = runtimeConfig.APP_CUSTOMER.web_sdk.enable_tagging_mode;
const playerInstance = computed<IPixellotPlayerExtended | null>(() =>
  playerRef.value ? playerRef.value.player : null,
);
const playerState = computed<IPlayerState | null>(() =>
  playerRef.value ? playerRef.value.state : null,
);
const playerTags = computed(() =>
  enableTaggingMode ? props.tags?.map(tag => ({ ...tag, time: tag.startTime })) : [],
);
const isPlaying = computed(() => playerState.value?.playing);
const activePPVTicket = computed(() => eventStore.activePPVTicket);
const playerAnalytics = computed<IAnalyticsOptions | null>(() => {
  if (!runtimeConfig.APP_YOUBORA_ACCOUNT_CODE || !props.event)
    return null;
  return {
    userID: userStore.user?.id,
    userType: userType.value,
    tenant: runtimeConfig.APP_CUSTOMER.web_sdk.tenant,
    appProjectId: runtimeConfig.APP_CUSTOMER.web_sdk.project_id,
    sportType: props.event.eventType || null,
    eventID: props.event.id || null,
    contentTitle: props.event.title,
    entityID: props.event.referenceId,
    entityType: props.event.eventType,
    contentIsLive: playerState.value?.isLive,
    videoMode: playerRef.value?.source.active,
    eventType: props.event.type || null,
    accountCode: runtimeConfig.APP_YOUBORA_ACCOUNT_CODE as "pixellot" | "pixellotdev",
  };
});

function onPlayerLoaded({ player, video, state }: IPlayerData) {
  player.on("volumechange", () => {
    defaultVolume.value = playerInstance.value?.volume();
  });

  player.on("error", () => {
    eventStore.playerError = state?.error?.message ?? ERROR_TYPE.MEDIA_ERR_SRC_NOT_SUPPORTED;
  });

  emit("mounted", { player, video, state });
}

function onPlayerUnloaded() {
  emit("unmounted");
}

function cleanSelectedTagId() {
  const el = document.getElementById("selectedTag");
  if (el)
    el.id = "";
}

function onClickOutsideTagPopover() {
  cleanSelectedTagId();
  selectedTagData.value = null;
  selectedTagSelector.value = null;
}

function getPopoverXPositionClass() {
  if (!selectedTagSelector.value)
    return;
  const tagTargetEl: HTMLElement | null = document.querySelector(selectedTagSelector.value);
  const playerEl: HTMLElement | null = (playerInstance.value?.el() as HTMLElement) || null;
  if (!tagTargetEl || !playerEl)
    return;
  return getFloatingPosition(tagTargetEl, playerEl, { position: "x" });
}

function onTagClick(data: { event: PointerEvent | MouseEvent; data: any }) {
  emit("tagClick", data);
  const targetEl = event?.target as HTMLDivElement | null;
  if (!targetEl)
    return;
  const tagTargetEl: HTMLDivElement | null = targetEl.closest(".vjs-marker");
  if (!tagTargetEl)
    return;
  cleanSelectedTagId();
  tagTargetEl.id = "selectedTag";
  selectedTagData.value = data.data?.tags ? data.data.tags : [data.data];
  selectedTagSelector.value = "#selectedTag";
}

function onTagEdit(tag: IModelTag) {
  if (playerInstance.value?.isFullscreen())
    playerInstance.value?.exitFullscreen();
  onClickOutsideTagPopover();
  emit("editTag", tag);
}

function onTagDelete(tag: IModelTag) {
  if (playerInstance.value?.isFullscreen())
    playerInstance.value?.exitFullscreen();
  onClickOutsideTagPopover();
  emit("deleteTag", tag);
}

defineExpose({
  player: playerInstance,
  state: playerState,
});

watch(
  () => playerRef.value?.player,
  (player, oldPlayer) => {
    if (oldPlayer) {
      window.removeEventListener("beforeunload", handleBeforeUnload);
    }

    if (!player)
      return;

    const extendedPlayer = player as IPixellotPlayerExtended;

    try {
      const mediaEl = extendedPlayer.el().querySelector("video, audio") as HTMLVideoElementWithAirPlay;
      if (mediaEl && mediaEl.webkitShowPlaybackTargetPicker) {
        mediaEl.webkitShowPlaybackTargetPicker();

        const handleWirelessChange = () => {
          if (mediaEl.webkitCurrentPlaybackTargetIsWireless) {
            segment.track(SEGMENT_TRACK_EVENTS.VIDEO_CAST_STARTED, {
              stream_id: props.event.id,
              type: "air_play",
            });
            mediaEl.removeEventListener(
              "webkitcurrentplaybacktargetiswirelesschanged",
              handleWirelessChange,
            );
          }
        };

        mediaEl.addEventListener("webkitcurrentplaybacktargetiswirelesschanged", handleWirelessChange);
      }
    }
    catch (error) {
      console.error("Failed to set up AirPlay:", error);
    }

    extendedPlayer.on("chromecastConnected", () => {
      segment.track(SEGMENT_TRACK_EVENTS.VIDEO_CAST_STARTED, {
        stream_id: props.event.id,
        type: "chromecast",
      });
    });

    function handleBeforeUnload() {
      if (extendedPlayer && extendedPlayer.chromecastSessionManager) {
        try {
          const castContext = extendedPlayer.chromecastSessionManager?.getCastContext();
          if (castContext) {
            castContext.endCurrentSession(true);
          }
        }
        catch (e) {
          console.error("Failed to disconnect Chromecast session:", e);
        }
      }
    };

    window.addEventListener("beforeunload", handleBeforeUnload);
  },
  { immediate: true },
);
</script>

<template>
  <EventPlayerError v-if="errorMessage" :error-message="errorMessage as ERROR_TYPE" />
  <Player
    v-else
    id="pixellotPlayer"
    ref="playerRef"
    v-slot="{ player, source, state, setSrc }"
    class="root-class"
    render-tag-groups
    :tags="playerTags"
    autoplay="any"
    pano-controls-selector="#panoControlsPlaceholder"
    :default-volume="defaultVolume"
    playsinline
    :slider-limit="180"
    :analytics="playerAnalytics"
    :ads="{
      enableManifestMonitoring: props.adsConfig?.enableManifestMonitoring ?? true,
      enableTimelineAdvancement: props.adsConfig?.enableTimelineAdvancement ?? true,
      manifestAdSource: props.adsConfig?.manifestAdSource,
      hd: props.event.ads,
    }"
    :src="{
      hd: props.event.source.hd,
      pano: props.event.source.pano,
    }"
    @mounted="onPlayerLoaded"
    @unmounted="onPlayerUnloaded"
    @tag-click="onTagClick"
  >
    <EventPlayerTopBar :player="player" :source="source" :state="state" :set-src="setSrc" />
    <Transition
      v-show="state.hasStarted && source.active === 'pano'"
      enter-from-class="opacity-0"
      leave-to-class="opacity-0"
      enter-active-class="transition duration-300"
      leave-active-class="transition duration-300"
    >
      <div
        v-show="(state.userActive || state.paused) && source.active === 'pano'"
        class="absolute bottom-[100px] left-4 gap-3 items-start hidden sm:flex"
        style="direction: ltr;"
      >
        <div v-show="isPanoControlsVisible" id="panoControlsPlaceholder" />
      </div>
    </Transition>
    <Teleport v-if="selectedTagSelector && selectedTagData?.length" :to="selectedTagSelector">
      <div
        v-on-click-outside="onClickOutsideTagPopover"
        class="absolute bottom-6 z-[100] w-[188px]"
        :class="getPopoverXPositionClass()"
      >
        <EventPlayerTagPopover :tags="selectedTagData" @edit-tag="onTagEdit" @delete-tag="onTagDelete" />
      </div>
    </Teleport>
    <CardPPVTicketIndicator
      v-if="activePPVTicket"
      :title="activePPVTicket.title"
      :class="[{ 'delay-[2000ms] duration-[2000ms] opacity-0': isPlaying }]"
    />
    <slot :player="player" :source="source" :state="state" :set-src="setSrc" />
  </Player>
</template>

<style lang="scss">
.root-class {
  position: relative;
  direction: ltr;

  .vjs-marker:hover {
    background-color: var(--base-customer-color);
  }

  #selectedTag {
    background-color: var(--base-customer-color);
  }
}

.pixellot-player .pxlt-navigation-area {
  padding: 0;
  background: none;
  border-radius: 4px;
  overflow: hidden;

  .pxlt-navigation-area-container {
    background: rgba(0, 0, 0, 0.4);
    box-shadow: none;
    border: none;
  }

  .pxlt-zoom-area {
    background: rgba(0, 0, 0, 0.4);
    box-shadow: none;
    border: none;
  }
}
</style>
