<script setup lang="ts">
import { UModalConfirm } from "#components";
import { useModal } from "~/composables/use-modal";
import TagsAPI from "~/services/tags";
import useEventStore from "~/stores/event";
import getErrorLocalePathMessage from "~/utils/locale";
import { getTagUrl } from "~/utils/tags";
import { formatTime } from "~/utils/time";

export interface TagInfoProps {
  tag: IModelTag;
  isDeleteButtonDisabled?: boolean;
  isEditButtonDisabled?: boolean;
}

const { t, te } = useI18n();
const modal = useModal();
const toaster = useToast();
const eventStore = useEventStore();
const props = defineProps<TagInfoProps>();

const pauseVideo = () => eventStore.player?.pause();
const playVideo = () => eventStore.player?.play();

const onError = (error: Error) => {
  const { message } = getErrorLocalePathMessage(error.message, { t, te });
  return toaster.error(message);
};

const onClickDelete = (tagId: string) => {
  eventStore.tags.isEditingTag = false;
  pauseVideo();

  modal.open(UModalConfirm, {
    heading: t("labels.modal_labels.delete_tag", 0),
    icon: "tag",
    confirmLabel: t("button.delete"),
    variant: "delete",
    confirmFn: () => {
      return TagsAPI.delete(tagId)
        .then(() => toaster.success(t("pages.events.tabs.event_tags.tag_deleting_successfully_text")))
        .then(() => playVideo())
        .catch(onError);
    },
  });
};

const onEditTag = () => {
  if (props.tag?.id && eventStore.tags.data.has(props.tag.id))
    eventStore.tags.activeEditTag = eventStore.tags.data.get(props.tag.id) || null;
  eventStore.tags.isEditingTag = true;
};
const tagSrc = getTagUrl(props.tag?.type);
const startTime = formatTime(props.tag?.startTime);
const endTime = formatTime(props.tag?.endTime);
const teamColor = computed(() => props.tag?.tagTeam?.color);
const playerNumber = computed(() => props.tag?.playerNumber);
</script>

<template>
  <div class="flex w-full justify-between">
    <div class="tagInfo__icon">
      <IconTagType :src="tagSrc" />
    </div>
    <div class="tagInfo__info">
      <span class="text-main text-caption-bold truncate">
        {{ props.tag?.title }}
      </span>
      <span class="tagInfo__info_time"> {{ startTime }} - {{ endTime }} </span>
    </div>
    <div class="tagInfo__menu">
      <div class="flex w-full justify-between mb-2">
        <BaseButton
          class="text-secondary text-link"
          type="icon"
          :disabled="props.isEditButtonDisabled"
          name="edit-button"
          @click="onEditTag"
        >
          <div class="flex items-center">
            <IconEdit :height="12" :width="12" />
          </div>
        </BaseButton>
        <BaseButton
          class="text-secondary text-link"
          type="icon"
          :disabled="props.isDeleteButtonDisabled"
          name="delete-button"
          @click="onClickDelete(props.tag?.id)"
        >
          <div class="flex items-center">
            <IconDelete :height="12" :width="12" />
          </div>
        </BaseButton>
      </div>
      <div class="w-full flex justify-end">
        <div
          v-if="teamColor && playerNumber"
          class="tagInfo__number text-main"
          :style="{ 'background-color': teamColor }"
        >
          {{ playerNumber }}
        </div>
      </div>
    </div>
  </div>
</template>

<style lang="scss">
.tagInfo__icon {
  @apply flex items-center w-[25%] h-full;
}

.tagInfo__info {
  @apply w-[55%] flex flex-col mx-1 justify-center h-full;

  .tagInfo__info_time {
    @apply truncate mt-1;
    font-size: 10px;
    color: var(--base-secondary-color);
  }
}
.tagInfo__menu {
  @apply w-[20%] h-full flex flex-col justify-center
    justify-start;

  .tagInfo__number {
    @apply h-3.5 w-3.5 flex items-center
      justify-center rounded-full truncate font-medium;
    font-size: 10px;
  }
}
</style>
