<script setup lang='ts'>
import type videojs from "video.js";
import type { DeepReadonly } from "vue";
import type { VideoJsPlayer as IPixellotPlayer, VideoJsPlayerOptions as IPixellotPlayerOptions, IPlayerControlLink, ITag as IPixellotTag, IAnalyticsOptions, TOptionalAnalyticsOptions, ITagGroup, AdSource, AdsConfig } from "@pixellot/web-sdk";
import type { IPlayerState } from "../util/state";
import type { IPixellotPlayerSource, IAdvertisementConfig } from "../types";
import { onBeforeUnmount, onMounted, ref, readonly, computed, shallowRef, watch } from "vue";
import { setupPlayer, version as playerVersion } from "@pixellot/web-sdk";
import { setupAdvertisementPlugin } from "@pixellot/web-sdk";
import "@pixellot/web-sdk/style.css";
import { createPlayerState } from "../util/state";
import useSource from '../composables/use-source'

const props = withDefaults(
  defineProps<{
  panoControlsSelector?: string;
  analytics?: IPixellotPlayerOptions['analytics'];
  controlLink?: IPlayerControlLink;
  title?: string;
  tags?: IPixellotTag[];
  sliderLimit?: number;
  renderTagGroups?: boolean;
  src: string | IPixellotPlayerSource;
  ads?: IAdvertisementConfig;
  trimmingToolShown?: boolean;
  seekButtons?: {
    isActive: boolean;
    onClickNext?: () => void;
    onClickPrevious?: () => void;
    isPreviousDisabled: boolean;
    isNextDisabled: boolean;
  },
  qualityHlsDeduplicateLevels?: boolean;
  // VideoJS options
  id?: string | undefined;
  aspectRatio?: string | undefined;
  autoplay?: videojs.Autoplay | undefined;
  defaultVolume?: number | undefined;
  fill?: boolean | undefined;
  fluid?: boolean | undefined;
  loop?: boolean | undefined;
  muted?: boolean | undefined;
  playsinline?: boolean | undefined;
  poster?: string | undefined;
  responsive?: boolean | undefined;
  switchType?: 'button' | 'selector';
  telestrationButtonShown?: boolean;
  telestrationModeEnabled?: boolean;
}>(),
{
  sliderLimit: 120
}
)

const emit = defineEmits<{
  // (event: EventKey): void;
  (event: 'mounted', data: { video: HTMLVideoElement; player: IPixellotPlayer; state: IPlayerState | null }): void;
  (event: 'unmounted'): void;
  (event: "tagClick", data: { event: PointerEvent | MouseEvent; data: IPixellotTag | ITagGroup }): void;
  (event: "tagTimeIntersect", data: { data: IPixellotTag | ITagGroup }): void;
  (event: "update:telestration-mode-enabled", newTelestrationValue: boolean): void;
}>();

const mounted = shallowRef(false)
const videoRef = ref<HTMLVideoElement | null>(null);
const player = shallowRef<IPixellotPlayer | null>(null);
const state = ref<IPlayerState | null>(null);
const { source, setSrc } = useSource(player, state);
const videoId = ref<string | null>(null);
const readOnlyState = computed<DeepReadonly<IPlayerState> | null>(() => {
  return state.value ? readonly(state.value) : null
})
const isPanoAvailable = computed(() => Boolean(source.value.pano) && source.value.active !== "pano");
const isAdsAvailable = computed(() => Boolean(props.ads && (
  props.ads.enableManifestMonitoring ||
  props.ads.hd?.length || 
  props.ads.pano?.length || 
  props.ads.tactical?.length || 
  props.ads.global?.length
)))
const isTelestrationButtonShown = computed(() => !!(props.telestrationButtonShown && videoRef.value));

onMounted(() => {
  if (!videoRef.value) {
    console.error("Player target element not found!");
    return;
  }

  // Create player
  const pixellotPlayer = setupPlayer(videoRef.value as HTMLVideoElement, {
    id: props.id,
    aspectRatio: props.aspectRatio || "16:9",
    autoplay: props.autoplay,
    defaultVolume: props.defaultVolume,
    fill: props.fill,
    fluid: props.fluid,
    loop: props.loop,
    muted: props.muted,
    playsinline: props.playsinline,
    poster: props.poster,
    responsive: props.responsive,
    html5: {
      vhs: {
        useCueTags: true
      }
    },
    // analytics: props.analytics,
    tags: {
      renderGroups: props.renderTagGroups,
      data: props.tags,
      onTagClick: (event, data) => emit("tagClick", { event, data }),
      onCurrentTimeIntersect: (data) => emit("tagTimeIntersect", { data })
    },
    trim: {
      sliderLimit: props.sliderLimit
    },
    qualityHls: {
      deduplicateLevels: Boolean(props.qualityHlsDeduplicateLevels)
    }
    // title: props.title,
    // controlLink: props.controlLink,
    // controlLink: {
    //   label: '123'
    // },
  });

  if (props.panoControlsSelector) {
    pixellotPlayer.panoControls({ elSelector: props.panoControlsSelector })
  }

  // TODO: Think if this is necessary
  // Stringing video.js events to vue emits.
  // events.forEach((eventKey) => {
  //   pixellotPlayer._videoJSPlayer.on(eventKey, (payload) => emit(eventKey))
  // })

  // Create player state.
  createPlayerState(pixellotPlayer, {
    onInit(initState) {
      state.value = initState;
    },
    onUpdate(key, value) {
      if (state.value) {
        state.value[key] = value;
      }
    }
  });

  player.value = pixellotPlayer;
  videoId.value = pixellotPlayer.el().id;
  mounted.value = true
  emit('mounted', {
    video: videoRef.value as HTMLVideoElement,
    player: player.value,
    state: readOnlyState.value
  })

  if (props.src)
    setSrc(props.src);

  // TODO: Fix this workaround
  if (props.controlLink)
    pixellotPlayer.controlLink(props.controlLink)

  if (props.analytics) {
    setAnalytics(props.analytics);
  }

  if (props.seekButtons) {
    pixellotPlayer.seekButtons(props.seekButtons);
  }

  if (isAdsAvailable.value) {
    setupAds();
  }
});

function setupAds() {
  if (!player.value || !props.ads) {
    return;
  }
  const adsConfig: AdsConfig = {
    enableTimelineAdvancement: props.ads.enableTimelineAdvancement ?? false,
    enableManifestMonitoring: props.ads.enableManifestMonitoring ?? false,
    enableDebugLogging: props.ads.enableDebugLogging ?? false,
    manifestAdSource: props.ads.manifestAdSource,
    adBreakTimingBuffer: props.ads.adBreakTimingBuffer,
    eventListeners: props.ads.eventListeners || {}
  };

  if (!player.value.adsAPI) {
    player.value.adsAPI = setupAdvertisementPlugin(player.value, adsConfig);
  }

  if (adsConfig.enableManifestMonitoring) {
    if (adsConfig.enableDebugLogging) {
      console.log('[Player] Manifest monitoring enabled - skipping legacy ad scheduling');
    }
    return;
  }

  const currentAds = getCurrentAds();

  if (currentAds && currentAds.length > 0) {
    player.value.adsAPI.scheduleAds(currentAds);
  }
}

function getCurrentAds(): AdSource[] {
  if (!props.ads) {
    return [];
  }

  const { active } = source.value;
  const sourceSpecificAds = active === 'pano' ? props.ads.pano : 
                          active === 'tactical' ? props.ads.tactical :
                          props.ads.hd;
  
  const globalAds = props.ads.global || [];
  const specificAds = sourceSpecificAds || [];
  
  return [...globalAds, ...specificAds];
}

function setAnalytics(options?: IPixellotPlayerOptions['analytics']) {
  if (!player.value)
    return;

  if (player.value.analyticsAPI) {
    if (options) {
      player.value.analyticsAPI.updateOptions(options as TOptionalAnalyticsOptions);
    } else {
      player.value.analyticsAPI.clearOptions();
      player.value.analyticsAPI.destroy();
      player.value.analyticsAPI = null;
    }
  } else {
    const payload = options ? Object.assign({ playerVersion }, options) as IAnalyticsOptions : null;
    player.value.analytics(payload);
  }
}

function onPanoButtonClick() {
  setSrc({ active: 'pano', isSwitching: true });

  player.value?.one('loadedmetadata', () => player.value?.panoAPI.switchMode('full'))
}

function updateSwitcherElement(show: boolean) {
  if (props.switchType === 'selector') {
    const items = [
      { type: 'tactical', value: source.value.tactical, onClick: () => setSrc({ active: 'tactical', isSwitching: true }) },
      { type: 'hd', value: source.value.hd, onClick: () => setSrc({ active: 'hd', isSwitching: true }) },
      { type: 'pano', value: source.value.pano, onClick: () => setSrc({ active: 'pano', isSwitching: true }) },
    ].filter(i => i.value);
    player.value?.sourceSelector({ items: items });
  } else {
    player.value?.panoBtn({ show, onClick: onPanoButtonClick });
  }
}

function switchTelestrationMode() {
  if (player.value) {
    player.value.telestrationAPI?.toggle(props.telestrationModeEnabled)
  }
}

// Reactively update the player, when relevant prop is changed
watch(() => props.title, (title) => player.value?.title(title || ""));
watch(() => props.controlLink, (link) => player.value?.controlLink(link || null));
watch(() => props.tags || [], (tags) => player.value?.tagsAPI.set(tags || []));
watch(() => props.seekButtons, (buttons) => player.value?.seekButtons(buttons || null), { deep: true });
watch(() => props.src, (src, oldSrc) => {
  if (JSON.stringify(oldSrc) !== JSON.stringify(src))
    setSrc(src)
});
watch(() => props.trimmingToolShown, (show) => player.value?.trimAPI.toggle(show));
watch(isPanoAvailable, (show) => updateSwitcherElement(show));
watch(() => props.analytics, (analyticsOptions) => setAnalytics(analyticsOptions))
watch(() => source.value.active, () => {
  if (!player.value?.adsAPI)
    return

  player.value?.adsAPI.resetAd();
  const ads = getCurrentAds();
  if (ads) {
    player.value.adsAPI.scheduleAds(ads as AdSource[])
  }
})
watch(
  [() => isTelestrationButtonShown.value, () => player.value],
  ([isShown, currentPlayer]) => {
    if (currentPlayer) {
      currentPlayer.telestrationBtn({
        onClick: () => emit("update:telestration-mode-enabled", !props.telestrationModeEnabled),
        isTelestrationButtonShown: isShown,
      });
    }
  },
);
watch(() => props.telestrationModeEnabled, switchTelestrationMode);

onBeforeUnmount(() => {
  if (player.value) {
    if (player.value.adsAPI) {
      try {
        player.value.adsAPI.dispose();
      } catch (error) {
        console.error('[Player] Error disposing ads API:', error);
      }
    }

    try {
      player.value.dispose();
    } catch (error) {
      console.error('[Player] Error disposing player:', error);
    }

    player.value = null;
    emit('unmounted');
  }
});

defineExpose({ video: videoRef, player, state: readOnlyState, source, setSrc });
</script>


<template>
  <div class="pixellot-player">
    <video ref="videoRef"></video>
    <Teleport v-if="videoId" :to="`#${videoId}`">
      <slot v-if="mounted" :video="videoRef!" :player="player!" :state="readOnlyState!" :source="source"
        :set-src="setSrc" />
    </Teleport>
  </div>
</template>

<style>
.pixellot-player {
  position: relative;
}
</style>