<script setup lang="ts">
import type { IPixellotPlayerSource } from "@pixellot/vue-player";
import type { IPlayerState } from "@pixellot/vue-player/src/util/state";
import type { VideoJsPlayer as IPixellotPlayer } from "@pixellot/web-sdk";

export interface TopBarInterface {
  player: IPixellotPlayer;
  source: IPixellotPlayerSource;
  state: IPlayerState;
  setSrc: (src: string | IPixellotPlayerSource) => IPixellotPlayerSource;
}

const { t } = useI18n();
const props = defineProps<TopBarInterface>();
const activeIndex = ref("0");
const toggleMode = () => activeIndex.value === "0" ? props.player.panoAPI.switchMode("full") : props.player.panoAPI.switchMode("restricted");

watch(activeIndex, toggleMode);
</script>

<template>
  <Transition
    v-if="state.hasStarted && source.active === 'pano' && !state.error"
    enter-from-class="opacity-0"
    leave-to-class="opacity-0"
    enter-active-class="transition duration-300"
    leave-active-class="transition duration-300"
  >
    <div
      v-show="state.userActive || state.paused"
      class="absolute top-0 left-0 flex items-center  w-full gap-2 px-4 py-3 bg-gradient-to-b from-[#000000c7] to-transparent h-20"
    >
      <div class="flex w-full h-full items-center relative">
        <div class="absolute inset-0 flex items-center justify-center">
          <div class="flex gap-2 text-[9px] sm:text-[11px]">
            <p>{{ t('labels.panoramic_mode') }}</p>
            <BaseButton name="exit" link type="primary" class="font-medium" @click="setSrc({ active: 'hd', isSwitching: true })">
              {{ t('button.exit') }}
            </BaseButton>
          </div>
        </div>
        <div class="absolute right-4 text-[11px]">
          <BaseTabs v-model:active-index="activeIndex" root-class="!mb-0">
            <BaseTab :heading="t('button.full')" />
            <BaseTab :heading="t('button.regular')" />
          </BaseTabs>
        </div>
      </div>
    </div>
  </Transition>
</template>
