<script setup lang="ts">
import { ERROR_TYPE, THEME_COLOR } from "~/constants";

export interface ErrorProps {
  errorMessage: ERROR_TYPE | null;
}

const { t, locale } = useI18n();
const color = useColorMode();
const props = defineProps<ErrorProps>();
const runtimeConfig = useRuntimeConfig().public;

const liveStreamErrorTranslations = runtimeConfig.APP_CUSTOMER.live_stream_error_message as unknown as ICustomerLiveStreamErrorMessage[];
const defaultLanguage = runtimeConfig.APP_CUSTOMER.default_language;
const isEnableLogoOnLiveStreamError = runtimeConfig.APP_CUSTOMER.enable_logo_on_live_stream_error;

const streamErrorMessage = computed(() => {
  if (Array.isArray(liveStreamErrorTranslations) && liveStreamErrorTranslations.length > 0) {
    return (
      liveStreamErrorTranslations.find(t => t.locale === locale.value)?.message
      || liveStreamErrorTranslations.find(t => t.locale === defaultLanguage)?.message
      || liveStreamErrorTranslations[0]?.message
    );
  }
  return t("errors.live_stream_error_message");
});

const errorMessage = computed(() => {
  switch (props.errorMessage) {
    case ERROR_TYPE.MEDIA_ERR_SRC_NOT_SUPPORTED:
      return t("errors.vod_corrupted_files");
    case ERROR_TYPE.MEDIA_ERR_NETWORK:
    case ERROR_TYPE.MEDIA_ERR_ABORTED:
    case ERROR_TYPE.MEDIA_ERR_ENCRYPTED:
      return t("errors.vod_playback_failed");
    case ERROR_TYPE.STREAM_ERR:
      return streamErrorMessage.value;
    default:
      return t("errors.vod_files_not_found");
  }
});

const isStreamingError = computed(() => props.errorMessage === ERROR_TYPE.STREAM_ERR);
const isPlaybackFailedError = computed(() => {
  return props.errorMessage !== null && [
    ERROR_TYPE.MEDIA_ERR_NETWORK,
    ERROR_TYPE.MEDIA_ERR_ABORTED,
    ERROR_TYPE.MEDIA_ERR_ENCRYPTED,
  ].includes(props.errorMessage);
});
const isVODCorruptedFilesError = computed(() => props.errorMessage === ERROR_TYPE.MEDIA_ERR_SRC_NOT_SUPPORTED);
</script>

<template>
  <div class="aspect-video bg-[var(--base-player-error-bgColor)]">
    <div class="flex h-full justify-center items-center">
      <div class="flex flex-col items-center px-10 gap-4">
        <BaseImage
          v-if="isEnableLogoOnLiveStreamError && isStreamingError"
          class="max-h-20 w-auto"
          :src="runtimeConfig.APP_CUSTOMER.appearance.logo_url"
          fallback-src="/images/mock/logo.svg"
          alt="Logo"
          width="auto"
          :height="156"
        />

        <IconPlayerLiveStreamError
          v-else-if="!isEnableLogoOnLiveStreamError && isStreamingError"
          :is-dark-theme="color.value === THEME_COLOR.DARK"
        />

        <IconPlayerPlaybackFailedError
          v-else-if="isPlaybackFailedError"
          :is-dark-theme="color.value === THEME_COLOR.DARK"
        />

        <IconPlayerVODCorruptedFilesError
          v-else-if="isVODCorruptedFilesError"
          :is-dark-theme="color.value === THEME_COLOR.DARK"
        />

        <IconPlayerVODFilesNotFoundError
          v-else
          :is-dark-theme="color.value === THEME_COLOR.DARK"
        />

        <p class="text-secondary whitespace-pre-wrap text-center text-xs sm:text-base xl:text-lg font-medium">
          {{ errorMessage }}
        </p>
      </div>
    </div>
  </div>
</template>
